.GameStats {
    position: absolute;
    width: 27vw;
    list-style: none;
    color: rgba(255, 255, 255, 0.5);
    padding: 0;
    margin: 0;
}

.GameStats__left {
    right: 0;
    top: 22vh;
    text-align: left;
}

.GameStats__right {
    right: 0;
    bottom: 0;
    text-align: left;
}



.GameStats .value {
    font-size: 2.8rem;
    margin-bottom: 0.5em;
    color: rgba(255, 255, 255, 1);
}